@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: TopazPlus-a1200;
  src: url("./topaz.eot");
}

@font-face {
  font-family: TopazPlus-a1200;
  src: url("./topaz.ttf") format("truetype");
}

body {
  margin: 0;
  padding: 0;
  background: #0a1f1c;
  min-height: 100vh;
  font-family: TopazPlus-a1200;
}

body {
  position: absolute;
  width: auto;
  min-width: 100%;
}

.flicker {
  pointer-events: none;
  position: fixed;
  width: auto;
  min-width: 100%;
  height: 100%;
  z-index: 15;
  background: rgba(9, 8, 8, 0.1);
  animation: flicker 0.3301s infinite;
}

.scanline {
  position: fixed;
  pointer-events: none;
  top: 0%;
  width: auto;
  min-width: 100%;
  height: 2px;
  z-index: 10;
  background: rgba(0, 0, 0, .3);
  opacity: 0.75;
  animation: scanline 7.77s linear infinite;
}

.scanlines {
  position: fixed;
  pointer-events: none;
  width: auto;
  min-width: 100%;
  height: 100%;
  z-index: 5;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  background-size: 100% 2px, 3px 100%;
}

@keyframes flicker {
  0% {
    opacity: 0.119;
  }

  5% {
    opacity: 0.02841;
  }

  10% {
    opacity: 0.35748;
  }

  15% {
    opacity: 0.88852;
  }

  20% {
    opacity: 0.9408;
  }

  25% {
    opacity: 0.35088;
  }

  30% {
    opacity: 0.22426;
  }

  35% {
    opacity: 0.26418;
  }

  40% {
    opacity: 0.09249;
  }

  45% {
    opacity: 0.35312;
  }

  50% {
    opacity: 0.89436;
  }

  55% {
    opacity: 0.9574;
  }

  60% {
    opacity: 0.19754;
  }

  65% {
    opacity: 0.05086;
  }

  70% {
    opacity: 0.12137;
  }

  75% {
    opacity: 0.75791;
  }

  80% {
    opacity: 0.89617;
  }

  85% {
    opacity: 0.90183;
  }

  90% {
    opacity: 0.20657;
  }

  95% {
    opacity: 0.64125;
  }

  100% {
    opacity: 0.78042;
  }
}


@keyframes scanline {
  0% {
    top: 99%;
  }
}

@keyframes scanlines {
  0% {
    background-position: 0 50%;
  }
}